import {
  Streamlit,
  StreamlitComponentBase,
  withStreamlitConnection,
} from "streamlit-component-lib"
import React, { ReactNode, ChangeEvent } from "react"

interface InputStyle {
  border: string;
  borderBottom: string;
  width: string;
  margin: string;
  outline: string;
}

interface State {
  inputValues: string[]
  inputStyles: InputStyle[]
}

class MyComponent extends StreamlitComponentBase<State> {

  public constructor(props: any) {
    super(props);
    const text = this.props.args["text"]
    const gapTokenQuantity = (text.match(/\[G_A_P\]/g) || []).length;
    this.state = { 
      inputValues: new Array(gapTokenQuantity).fill(''), 
      inputStyles: new Array(gapTokenQuantity).fill({
        border: 'none', 
        borderBottom: '1px solid black',
        width: "15%",
        margin: "0px 5px", 
        outline: "none"
      }) 
    };
  }

  public render = (): ReactNode => {
    const text = this.props.args["text"]
    const gapToken = "[G_A_P]"
    
    let splitText = text.split(gapToken)

    let components = [] 

    for(let i = 0; i < splitText.length; i++){
      components.push(<span>{splitText[i]}</span>)
      if(i !== splitText.length-1) {
        components.push(
          <input
            type="text"
            style={this.state.inputStyles[i]}
            onChange={(e) => this.onChange(e, i)}
            onMouseEnter={() => this.onMouseEnter(i)}
            onMouseLeave={() => this.onMouseLeave(i)}
            value={this.state.inputValues[i]}
          />
        )
      }
    }
    return (
      <span>
        {components}
      </span>
    )
  }

  private onChange = (e: ChangeEvent<HTMLInputElement>, index: number): void => {
    let inputValues = [...this.state.inputValues];
    inputValues[index] = e.target.value;
    this.setState({ inputValues: inputValues }, () => Streamlit.setComponentValue(this.state.inputValues));
  }

  private onMouseEnter = (index: number): void => {
    let inputStyles = [...this.state.inputStyles];
    inputStyles[index] = {...inputStyles[index], borderBottom: "1px solid #DBB8FF"};
    this.setState({ inputStyles: inputStyles });
  }

  private onMouseLeave = (index: number): void => {
    let inputStyles = [...this.state.inputStyles];
    inputStyles[index] = {...inputStyles[index], borderBottom: "1px solid black"};
    this.setState({ inputStyles: inputStyles });
  }
}

export default withStreamlitConnection(MyComponent)